.prices {
	padding: 80px 0 110px 0;
	background: $yellow-gradient;
	.block-title {
		text-align: center;
		margin: 0 0 20px 0; }
	.inner {
		@include flex(row, between, end, wrap);
		&-left {
			max-width: 285px;
			width: 100%; }
		&-right {
			max-width: 842px;
			width: 100%; }
		&__title {
			font: 700 24px/1.3 'Exo', sans-serif;
			margin: 0 0 15px 0; } }
	.items {}
	.item {
		@include flex(row, null, center, wrap);
		margin: 0 0 15px 0;
		&-icon {
			width: 70px;
			height: 70px;
			border-radius: 100%;
			background: white;
			@include flex(row, center, center, wrap);
			margin: 0 15px 0 0; }
		&__img {} }
	.btn-dark {
		margin: 15px 0 0 0;
		width: 100%; }

	.table {
		// +flex(row, null, null, wrap)
		&-column {
			// +flex(column, null, null, nowrap)
			&:not(:first-child) {
				.table__text {
					text-align: center; } } }
		&-td {
			flex: 1 1 auto;
			width: 168px;
			border-bottom: 1px solid lighten($dark, 65%);
			background: white;
			padding: 16px 24px;
			&:not(:last-child) {
				border-right: 1px solid lighten($dark, 65%); } }
		&__text {
			font-size: 14px; }
		&__title {
			text-transform: uppercase;
			font-size: 9px;
			letter-spacing: 0.1em;
			text-align: center;
			opacity: .5; }
		&__price {
			margin: 8px 0 0 0;
			text-align: center;
			font: 600 16px/1 'Exo', sans-serif; } } }


